.tooltip {
  background-color: $black-20;
  border-radius: 4px;
  box-shadow: 0 0 10px rgb(0 0 0 / 25%);
  color: $white;
  display: inline-block;
  opacity: 0;
  position: relative;

  &::before {
    border-color: transparent;
    border-style: solid;
    content: '';
    display: block;
    height: 0;
    position: absolute;
    width: 0;
  }

  &.is-top-arrow {
    margin-top: 8px;

    &::before {
      border-bottom-color: $black-20;
      border-width: 0 8px 8px;
      bottom: 100%;
    }
  }

  &.is-bottom-arrow {
    margin-top: -8px;

    &::before {
      border-top-color: $black-20;
      border-width: 8px 8px 0;
      top: 100%;
    }
  }

  &.is-top-arrow,
  &.is-bottom-arrow {
    &::before {
      left: 50%;
      margin-left: -8px;
    }

    &.is-left {
      margin-left: -10px;

      &::before {
        left: 2px;
        margin: 0;
      }
    }

    &.is-right {
      margin-left: 10px;

      &::before {
        left: auto;
        margin: 0;
        right: 2px;
      }
    }
  }

  &.is-right-arrow {
    margin-left: -6px;

    &::before {
      border-left-color: $black-20;
      border-width: 6px 0 6px 6px;
      left: 100%;
    }
  }

  &.is-left-arrow {
    margin-left: 6px;

    &::before {
      border-right-color: $black-20;
      border-width: 6px 6px 6px 0;
      right: 100%;
    }
  }

  &.is-right-arrow,
  &.is-left-arrow {
    &::before {
      margin-top: -6px;
      top: 50%;
    }

    &.is-top {
      margin-top: -8px;

      &::before {
        margin: 0;
        top: 2px;
      }
    }

    &.is-bottom {
      margin-top: 8px;

      &::before {
        bottom: 2px;
        margin: 0;
        top: auto;
      }
    }
  }
}
